body {
  margin: 0;
  font-family: -apple-system, 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #ffffff;
  min-width: 900px;
}



.wrapper {
  max-width: 900px;
  background: #ffffff;
  margin: 50px auto;
}

header {
  padding-bottom: 25px;
  border-bottom: 2px solid #D44A43;
}

.banner {
  max-width: 900px;
  height: 270px;
  margin-top: 45px;

  background: #D44A43;
  border-radius: 30px;

  h1 {
    font-size: 25px;
    line-height: 30px;
    width: 320px;
    margin-bottom: 80px;
    margin-left: 70px;
    position: absolute;
  }

  span {
    color: #fff;
  }

  button {
    width: 160px;
    height: 46px;
    border: none;
    background: #FFFFFF;
    border-radius: 8px;
    margin-left: 70px;
    margin-top: 100px;
    transition: box-shadow 0.2s ease-in-out, transform 0.2s ease-in-out;
    cursor: pointer;

    &:hover {
      box-shadow: 0px 14px 30px rgba(0, 0, 0, 0.05);
      transform: translateY(-2px);
    }

    p {
      padding: 0;
      font-weight: bold;
      text-transform: uppercase;
      color: #D44A43;
      font-size: 12px;
    }
  }
}

li {
  font-weight: bold;
  font-size: 12px;
  text-transform: uppercase;
  color: black;
  cursor: pointer;

  &:hover {
    color: #D44A43;
  }
}

.content {
  margin-left: 70px;

  .grey {
    color: #6a6a6a;
    cursor: pointer;

    &:hover {
      color: #000;
    }
  }

  span {
    color: #D44A43;
  }

  h1 {
    font-size: 25px;
    line-height: 30px;
    width: 320px;
    margin-top: 40px;
  }

  h3 {
    font-size: 17.6px;
    width: 900px;
  }
}

.text-1 {
  width: 730px;
  background: #FFFFFF;
  box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.25);
  border-radius: 30px;
  margin-top: 30px;
  margin-left: 70px;

  p {
    font-size: 17.6px;
    margin: 0;
    padding: 0 10px 15px 40px;
  }

  .title-red {

    width: 220px;
    height: 45px;
    border: none;
    background: #D44A43;
    border-radius: 0 10px 10px 0;
    margin: 30px 0 30px 0;

    h3 {
      color: #fff;
      font-weight: bold;
      font-size: 17.6px;
      margin: 0;
    }
  }
}

.exercise-2 {
